
html{
  background-color: #282c34;
  text-decoration: none
}
a{
  text-decoration: none;
}
body {
  margin: 0;
  padding:0 !important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}
#root{
  margin-left:200px;
  background-color: #282c34;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.top{
height: 150px;
}
.top h1{
  color:white;
  border-bottom: 0;
  text-align: center;
  padding-top: 51px !important
  }
.side{
  position: fixed;
  top: 0;
  left: 0;
  background-color: #61dafb;
  bottom: 0;
}
.photo{
display: block;
height: 250px;
top:0;
left:0;
width: 200px;
bottom:180px;
}
.photo a{
  margin-top: 62px;
  margin-left: 9px;
  display: inline-block;
  border: 1px solid #ededed;
  border-radius: 50%;
  overflow: hidden;
  width: 180px;
  height: 180px;
}

.nav{

  top: 180px;
  left:0;
  bottom:0;
  width: 200px;
  text-align: center;
  display: block;
  background-color: #61dafb;
  padding-top: 20px;
  padding-bottom: 20px
}
.nav a{
  color:white;
  display: block;
  font-size: 19px;
}
.route-content{
  padding:25px;
  background-color: white;
  min-height: 40em;
}